body {
  background: pink;
}

.App {
  text-align: center;
  margin: auto;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

h1.top {
  margin-top: 10vh;
  width: 400px;
  align-self: center;
}

button.fact {
  border: 2px solid #d24d57;
  width: 90px;
  align-self: center;
  border-radius: 4px 8px;
}

div.power {
  margin-top: 40px;
  padding-top: 20px;
  width: 720px;
  height: auto;
  word-wrap: break-word;
  align-self: center;
  font-family: "Times New Roman", Times, serif;
  font-size: 20px;
  background-color: aquamarine;
}

/*********** Tablets ************/
@media (max-width: 768px) {
  .App {
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
  }

  h1 {
    align-self: center;
  }

  div.power {
    padding: 20px;
    margin: 20px;
    width: auto;
    background-color: lightblue;
    font-family: "Times New Roman", Times, serif;
    font-weight: 250;
  }
}

@media (max-width: 560px) {
  .App {
    padding-top: 2vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
    word-wrap: normal;
  }

  h1.top {
    align-self: center;
    width: 200px;
    word-wrap: break-word;
    margin: auto;
    height: auto;
    padding-bottom: 10px;
  }

  div.power {
    width: 80%;
    background-color: lightblue;
    font-family: "Times New Roman", Times, serif;
    font-weight: 250;
    align-self: center;
  }
}
